:root {
  --font-montserrat: Montserrat, Helvetica, sans-serif;
}

html {
  margin: 0;
  padding: 0;
}

body, html {
  box-sizing: border-box;
  background-color: #f7f1e2;
  border: 0;
  max-height: 100vh;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-montserrat);
  margin: 0 auto;
  padding: .75rem;
  transition: all 1s;
}

.header {
  background-color: #ded0b6;
  border-bottom: 1px solid #b6bbc4;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto 2rem;
  padding: 1.5rem 1rem;
  display: flex;
  position: relative;
}

.category {
  pointer-events: none;
  font-weight: 600;
  display: flex;
}

.branding {
  pointer-events: none;
  font-weight: 600;
}

.main {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 0;
  display: flex;
  position: relative;
}

.back-btn {
  cursor: pointer;
  width: 35px;
  margin-bottom: 20px;
  display: none;
}

.section-wrapper {
  max-width: 1100px;
  margin: 0 auto;
}

.section {
  scrollbar-width: thin;
  scrollbar-color: #ded0b6 #808a71;
  background-color: #c4c3b0;
  border-top: 2px solid #000;
  border-bottom: 2px solid #000;
  flex-direction: column;
  justify-content: space-between;
  max-height: 100vh;
  margin: 0 auto;
  padding-top: 20px;
  display: none;
  position: relative;
  overflow-y: scroll;
}

.viewSection {
  max-width: 1400px;
  margin: 0 auto 20px;
  display: none;
  position: relative;
}

.left-pannel {
  flex: 1;
  max-width: 700px;
  padding: 0 20px;
  position: relative;
}

.right-pannel {
  flex: 1;
  justify-content: right;
  padding: 0 20px;
  display: flex;
}

.title {
  background-color: #f2c297;
  flex-direction: column;
  max-width: 700px;
  height: 100px;
  margin: 0 auto 20px;
  padding: 1rem 2rem;
  display: flex;
  position: relative;
}

.author {
  background-color: #b2c2b7;
  flex-direction: column;
  max-width: 700px;
  height: 100px;
  margin: 0 auto 20px;
  padding: 1rem 2rem;
  display: flex;
  position: relative;
}

.description {
  background-color: #c1b1be;
  flex-direction: column;
  max-width: 700px;
  height: 200px;
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  position: relative;
}

.buy-book {
  justify-content: center;
  align-items: center;
  max-width: 700px;
  height: 100px;
  display: flex;
  position: relative;
}

.buy-container {
  text-align: center;
  background-color: #c4c3b0;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  width: 150px;
  padding: .5rem 0;
  font-size: 24px;
}

.cover-image {
  opacity: .9;
  filter: drop-shadow(0 5px 15px #000);
  border-radius: 6px;
  width: 100%;
  height: 100%;
}

.img-container {
  justify-content: center;
  align-items: center;
  max-width: 90rem;
  max-height: 45rem;
  display: flex;
  position: relative;
}

.interaction-container {
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  display: flex;
  position: absolute;
}

.interaction-label {
