  width: 11.875rem;
  margin-bottom: 1rem;
  padding-bottom: 15rem;
  font-size: 1.875rem;
  font-weight: 600;
  display: flex;
  position: absolute;
}

.btn-find {
  color: #000;
  cursor: pointer;
  background-color: #b2a59b;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  width: 190px;
  height: 43px;
  font-size: 1.5rem;
  font-weight: 400;
  text-decoration: none;
  display: flex;
  position: absolute;
  bottom: .05rem;
  box-shadow: 7px 7px 3px #00000080;
}

.btn-find:hover {
  width: 190px;
  height: 43px;
  font-size: 1.6rem;
  box-shadow: 0 0 20px #000000e6;
}

#container {
  border-top: 1px solid #000;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  padding: 20px;
  display: flex;
}

