  flex-direction: column;
  display: flex;
}

.bubbles1 {
  justify-content: right;
  width: 100%;
  padding: 0 20px 20px 0;
  display: flex;
  position: relative;
}

.bubbles1 div {
  border-radius: 50%;
}

.bubbles1 div:first-child {
  background-color: #b2c2b7;
}

.bubbles1 div:nth-child(2) {
