}

.bubbles1 div:nth-child(3) {
  background-color: #f2c297;
}

.bubbles2 {
  justify-content: left;
  width: 100%;
  padding: 20px 0;
  display: flex;
  position: relative;
}

.bubbles2 div {
  background-color: #b2a59b;
  border-radius: 50%;
