.bubbles2 div:nth-child(2) {
  background-color: #c1b1be;
}

.bubbles2 div:first-child {
  background-color: #f2c297;
}

.bubbles1 div, .bubbles2 div {
  width: 1rem;
  height: 1rem;
}

.bubbles1, .bubbles2 {
  gap: .5rem;
}

.bubbles1, .bubbles2, .main, .section {
  max-width: 1300px;
}

.header, .footer {
  max-width: 1440px;
  transition: all 1s;
}

.footer {
  text-align: center;
  background-color: #ded0b6;
